import {Component, OnInit} from '@angular/core';
import {IAppAdmin} from '../../Models/app-admin';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../Services/app-admin.service';
import {ToastaService} from 'ngx-toasta';
import {DeleteRoleComponent} from '../roles/delete-role/delete-role.component';
import {IFarms} from '../../Models/farms';
import {FarmsService} from '../../Services/farms.service';

@Component({
    selector: 'app-farms',
    templateUrl: './farms.component.html',
    styleUrls: ['./farms.component.css']
})
export class FarmsComponent implements OnInit {

    cols: any;
    products: any;
    roles: IAppAdmin[];
    farms: IFarms[];
    rolesPermations: any[];
    display: boolean = false;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'id', header: 'ID'},
            {field: 'admin', header: 'Name'},
                {field: 'code', header: 'Code'},
            {field: 'farmed_type', header: 'Farmed Type'},
            {field: 'farmed_type_class', header: 'Farmed Type Class'}
        ];

        this.getFarmsApp();
    }

    showDialog(data: any) {
        this.rolesPermations = data.permissions;
        console.log(data.permissions);
        this.display = true;
    }

    getFarmsApp() {
        this._FarmsService.getFarms().subscribe((res: { data }) => {
            this.farms = res.data.all as IFarms[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }

    openRoleDeleteDialog(id) {
        const dialogRef = this.dialog.open(DeleteRoleComponent, {
            data: {id}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteMember(result);

            }
        });
    }

    onDeleteMember(roleId) {
        this._AppAdminService.deleteRole(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

}
