import {Component, OnInit} from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IAddFarmedTypeClass, IFarmedType} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddTypeComponent} from '../farmed-type/add-type/add-type.component';
import {DeleteTypeComponent} from '../farmed-type/delete-type/delete-type.component';
import {AddEditClassComponent} from './add-edit-class/add-edit-class.component';
import {DeleteClassComponent} from './delete-class/delete-class.component';

@Component({
    selector: 'app-farmed-type-class',
    templateUrl: './farmed-type-class.component.html',
    styleUrls: ['./farmed-type-class.component.css']
})
export class FarmedTypeClassComponent implements OnInit {

    cols: any;
    products: any;
    roles: IAppAdmin[];
    farms: IFarmedType[];
    profileFarm: IFarmedType;
    rolesPermations: any[];
    display: boolean = false;
    data: any;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };
    errorMessage: string;
    profileFarmData: IAddFarmedType;

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'farmed_type_id', header: 'ID'},
            {field: 'farmed_type_name', header: 'Farmed Type Name'},
            {field: 'name_ar_localized', header: 'Class Name'}


        ];
        this.getFarmsApp();
    }

    showDialogGetFarmedTypeById(id: number) {
        this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
            this.profileFarm = res.data as IFarmedType;
            console.log(this.profileFarm);
        }, error => {

        }, () => {
            this.display = true;

        });
    }

    getFarmsApp() {
        this._FarmsService.getFarmedTypeClass().subscribe((res: { data }) => {
            this.farms = res.data.all as IFarmedType[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }


    openAddFarmedTypeDialog() {

        const dialogRef = this.dialog.open(AddEditClassComponent, {
                data: {
                    FarmedData: this._FarmsService.AddFarmTypeClass, error: this.errorMessage,
                }
            })
        ;
        dialogRef.afterClosed().subscribe(result => {
            this.errorMessage = null;

            if (result) {
                this._FarmsService.AddFarmTypeClass = result.FarmedData;
                this.onAddFarmedType();

            } else {
                this._FarmsService.AddFarmTypeClass = {} as IAddFarmedTypeClass;

            }
        });
    }

    onAddFarmedType() {
        this._FarmsService.AddFarmedTypeClass().subscribe(res => {
        }, error => {

            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    getDataById(id: number) {
        this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
            this.profileFarmData = res.data as IAddFarmedType;
            console.log(this.profileFarmData);
        }, error => {

        }, () => {
            // this.display = true;
        });


    }

    openEditFarmedTypeDialog(classData: IAddFarmedTypeClass) {


        const dialogRef = this.dialog.open(AddEditClassComponent, {
            data: {FarmedData: classData}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this._FarmsService.AddFarmTypeClass = result.FarmedData;
                this.onEditFarmedType(result.FarmedData.id);

            } else {
                this._FarmsService.AddFarmTypeClass = {} as IAddFarmedTypeClass;
            }
        });
    }

    onEditFarmedType(roleId) {
        this._FarmsService.editFarmedTypeClass(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this._FarmsService.AddFarmTypeClass = {} as IAddFarmedTypeClass;

            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    openFarmedTypeDeleteDialog(data) {
        const dialogRef = this.dialog.open(DeleteClassComponent, {
            data: {farmData: data}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteFarmedType(result);

            }
        });
    }

    onDeleteFarmedType(roleId) {
        this._FarmsService.deleteFarmedTypeClass(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

}
