import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {PermeationsComponent} from '../permeations/permeations.component';
import {ViewPermeationComponent} from '../permeations/view-permeation/view-permeation.component';
import {UpdatePermeationComponent} from '../permeations/update-permeation/update-permeation.component';
import {FarmsComponent} from './farms.component';
import {ViewFarmComponent} from './view-farm/view-farm.component';
import {SettingFarmComponent} from './setting-farm/setting-farm.component';
import {FarmedTypeComponent} from './farmed-type/farmed-type.component';
import {FarmedTypeClassComponent} from './farmed-type-class/farmed-type-class.component';
import {FarmingWayComponent} from './farming-way/farming-way.component';
import {FarmingMethodsComponent} from './farming-methods/farming-methods.component';
import {SeedlingSourcesComponent} from './seedling-sources/seedling-sources.component';
import {AnimalBreedingComponent} from './animal-breeding/animal-breeding.component';
import {AnimalFodderComponent} from './animal-fodder/animal-fodder.component';
import {AnimalMedicineComponent} from './animal-medicine/animal-medicine.component';
import {PlantPotSizesComponent} from './plant-pot-sizes/plant-pot-sizes.component';
import {SaltTypesComponent} from './salt-types/salt-types.component';
import {SoilTypesComponent} from './soil-types/soil-types.component';
import {AcidityTypesComponent} from './acidity-types/acidity-types.component';
import {AnimalFodderSourcesComponent} from './animal-fodder-sources/animal-fodder-sources.component';
import {ChemicalFertilizerSourcesComponent} from './chemical-fertilizer-sources/chemical-fertilizer-sources.component';
import {HomePlantIlluminatingSourcesComponent} from './home-plant-illuminating-sources/home-plant-illuminating-sources.component';
import {IrrigationWaysComponent} from './irrigation-ways/irrigation-ways.component';
import {AddTypeComponent} from './farmed-type/add-type/add-type.component';

const routes: Routes = [
  {path: '', component: FarmsComponent},
  {path: 'view-farm', component: ViewFarmComponent},
  {path: 'setting-farm', component: SettingFarmComponent},
  {path: 'farmed-type', component: FarmedTypeComponent},
  {path: 'add-farmed-type', component: AddTypeComponent},
  {path: 'farmed-type-class', component: FarmedTypeClassComponent},
  {path: 'farming-ways', component: FarmingWayComponent},
  {path: 'farming-method', component: FarmingMethodsComponent},
  {path: 'seedling_sources', component: SeedlingSourcesComponent},
  {path: 'animal_breeding_purposes', component: AnimalBreedingComponent},
  {path: 'animal_fodder', component: AnimalFodderComponent},
  {path: 'animal_medicine', component: AnimalMedicineComponent},
  {path: 'plant_pot', component: PlantPotSizesComponent},
  {path: 'salt-types', component: SaltTypesComponent},
  {path: 'soil-types', component: SoilTypesComponent},
  {path: 'acidity_types', component: AcidityTypesComponent},
  {path: 'animal_fodder_sources', component: AnimalFodderSourcesComponent},
  {path: 'chemical_fertilizer_sources', component: ChemicalFertilizerSourcesComponent},
  {path: 'irrigation_ways', component: IrrigationWaysComponent},
  {path: 'plant_illuminating_sources', component: HomePlantIlluminatingSourcesComponent},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FarmsRoutingModule { }
