import {Component, OnInit} from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IFarmActivity, IFarmedType, IFarms} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {DeleteRoleComponent} from '../../roles/delete-role/delete-role.component';
import {AddTypeComponent} from './add-type/add-type.component';
import {DeleteTypeComponent} from './delete-type/delete-type.component';

@Component({
    selector: 'app-farmed-type',
    templateUrl: './farmed-type.component.html',
    styleUrls: ['./farmed-type.component.css']
})
export class FarmedTypeComponent implements OnInit {

    cols: any;
    products: any;
    roles: IAppAdmin[];
    farms: IFarmedType[];
    profileFarm: IFarmedType;
    rolesPermations: any[];
    display: boolean = false;
    data: any;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };
    errorMessage: string;
    profileFarmData: IAddFarmedType;

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'farm_activity_type_name', header: 'Farmed Type'},
            {field: 'name_ar_localized', header: 'Name'},
            {field: 'flowering_time', header: 'Flowering'},
            {field: 'farming_temperature', header: 'Flowering(Temp)'},
            {field: 'maturity_time', header: 'Maturity Time'},
            {field: 'maturity_temperature', header: 'Maturity(Temp)'},
            {field: 'suitable_soil_salts_concentration', header: 'Soil Salts'},
            {field: 'suitable_water_salts_concentration', header: 'Water Salts'},
            {field: 'suitable_ph', header: 'Ph'},
            {field: 'suitable_soil_types', header: 'Soil Types'},
        ];
        this.getFarmsApp();
    }

    showDialogGetFarmedTypeById(id: number) {
        this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
            this.profileFarm = res.data as IFarmedType;
            console.log(this.profileFarm);
        }, error => {

        }, () => {
            this.display = true;

        });
    }

    getFarmsApp() {
        this._FarmsService.getFarmedType().subscribe((res: { data }) => {
            this.farms = res.data.all as IFarmedType[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }


    openAddFarmedTypeDialog() {

        const dialogRef = this.dialog.open(AddTypeComponent, {
                data: {
                    FarmedData: this._FarmsService.AddFarmType, error: this.errorMessage,
                }
            })
        ;
        dialogRef.afterClosed().subscribe(result => {
            this.errorMessage = null;

            if (result) {
                this._FarmsService.AddFarmType = result.FarmedData;
                this.onAddFarmedType();

            } else {
                this._FarmsService.AddFarmType = {} as IAddFarmedType;

            }
        });
    }

    onAddFarmedType() {
        this._FarmsService.AddFarmedType().subscribe(res => {
        }, error => {

            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    getDataById(id: number) {
        this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
            this.profileFarmData = res.data as IAddFarmedType;
            console.log(this.profileFarmData);
        }, error => {

        }, () => {
            // this.display = true;
        });


    }

    openEditFarmedTypeDialog(farmedType: IAddFarmedType) {

        const dialogRef = this.dialog.open(AddTypeComponent, {
            data: {FarmedData: farmedType}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result.FarmedData);
                this._FarmsService.AddFarmType = result.FarmedData;

                // this.eventsList.id = result;
                this.onEditFarmedType(result.FarmedData.id);

            } else {
                this._FarmsService.AddFarmType = {} as IAddFarmedType;
            }
        });
    }

    onEditFarmedType(roleId) {
        console.log(this._FarmsService.AddFarmType.photo);
        this._FarmsService.editFarmedType(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this._FarmsService.AddFarmType = {} as IAddFarmedType;

            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    openFarmedTypeDeleteDialog(data) {
        const dialogRef = this.dialog.open(DeleteTypeComponent, {
            data: {farmData: data}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteFarmedType(result);

            }
        });
    }

    onDeleteFarmedType(roleId) {
        this._FarmsService.deleteType(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    receiveData(data: File) {
        console.log('Picked date: ', data);
    }
}
