import {Component, EventEmitter, Inject, OnInit, Output} from '@angular/core';
import {IFarmActivity} from '../../../../Models/farms';
import {FarmsService} from '../../../../Services/farms.service';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-edit-type',
  templateUrl: './edit-type.component.html',
  styleUrls: ['./edit-type.component.css']
})
export class EditTypeComponent implements OnInit {

  imagePath: any;
  fileToUpload: File = null;
  // farmActivities: IFarmActivity[];
  farmActivity: IFarmActivity[];
  min: number;
  max: number;
  @Output() newItemEvent: EventEmitter<File> = new EventEmitter();

  constructor(public _FarmsService: FarmsService, public dialogRef: MatDialogRef<EditTypeComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
  }

  addNewItem(value: File) {
    this.newItemEvent.emit(value);
  }

  ngOnInit(): void {
    this.getFarmsActivity();
  }

  onImageChange(event) {
    const reader2 = new FileReader();
    if (event.target.files && event.target.files.length) {
      const [file] = event.target.files;
      reader2.readAsDataURL(file);
      reader2.onload = () => {
        // this.imageSrc = reader2.result as string;
        this.fileToUpload = event.target.files.item(0);
        this.addNewItem(this.fileToUpload);
      };
    }
  }

  onSelectFile(event) {
    // called each time file input changes
    console.log(event);
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();

      const [file] = event.target.files;
      // reader2.readAsDataURL(file);
      reader.readAsDataURL(file);

      // tslint:disable-next-line:no-shadowed-variable
      reader.onload = () => {
        this.fileToUpload = event.target.files.item(0);

        this.imagePath = event.target.result;
        console.log(this.fileToUpload);
        this.data.FarmedData.photo = this.fileToUpload;
        console.log(this.data.FarmedData.photo);


      };

    }


  }

  getFarmsActivity() {
    this._FarmsService.getFarm_activity().subscribe((res: { data }) => {
      this.farmActivity = res.data.all as IFarmActivity[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }

  onNoClick(): void {

    this.dialogRef.close();
  }
}
