import { Component, OnInit } from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IAddFarmingWays, IFarmedType} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddWayComponent} from '../farming-way/add-way/add-way.component';
import {DeleteWayComponent} from '../farming-way/delete-way/delete-way.component';
import {DeleteSaltTypesComponent} from './delete-salt-types/delete-salt-types.component';
import {AddEditSaltTypesComponent} from './add-edit-salt-types/add-edit-salt-types.component';

@Component({
  selector: 'app-salt-types',
  templateUrl: './salt-types.component.html',
  styleUrls: ['./salt-types.component.css']
})
export class SaltTypesComponent implements OnInit {

  cols: any;
  products: any;
  roles: IAppAdmin[];
  farms: IAddFarmingWays[];
  profileFarm: IFarmedType;
  rolesPermations: any[];
  display: boolean = false;
  data: any;
  // toastOption: ToastOptions = {
  //     title: 'Events',
  //     msg: 'This Event Has Been Deleted successfully!',
  //     showClose: true,
  //     timeout: 3000,
  //     theme: 'material'
  // };
  errorMessage: string;
  profileFarmData: IAddFarmedType;

  constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
  }

  //
  ngOnInit(): void {
    this.cols = [
      {field: 'id', header: 'ID'},
      {field: 'name_ar_localized', header: 'Salt Type (Ar)'},
      {field: 'name_en_localized', header: 'Salt Type (En)'},
      {field: 'type', header: 'Type'}


    ];
    this.getFarmsApp();
  }

  // showDialogGetFarmedTypeById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarm = res.data as IFarmedType;
  //         console.log(this.profileFarm);
  //     }, error => {
  //
  //     }, () => {
  //         this.display = true;
  //
  //     });
  // }

  getFarmsApp() {
    this._FarmsService.getSalt_types().subscribe((res: { data }) => {
      this.farms = res.data as IAddFarmingWays[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }


  openAddFarmedTypeDialog() {

    const dialogRef = this.dialog.open(AddEditSaltTypesComponent, {
          data: {
            FarmedData: this._FarmsService.AddSaltTypes, error: this.errorMessage,
          }
        })
    ;
    dialogRef.afterClosed().subscribe(result => {
      this.errorMessage = null;

      if (result) {
        this._FarmsService.AddSaltTypes = result.FarmedData;
        this.onAddFarmedType();

      } else {
        this._FarmsService.AddSaltTypes = {} as IAddFarmingWays;

      }
    });
  }

  onAddFarmedType() {
    this._FarmsService.AddSalt_types().subscribe(res => {
    }, error => {

      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  // getDataById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarmData = res.data as IAddFarmedType;
  //         console.log(this.profileFarmData);
  //     }, error => {
  //
  //     }, () => {
  //         // this.display = true;
  //     });
  //
  //
  // }

  openEditFarmedTypeDialog(classData: IAddFarmingWays) {


    const dialogRef = this.dialog.open(AddEditSaltTypesComponent, {
      data: {FarmedData: classData}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this._FarmsService.AddSaltTypes = result.FarmedData;

        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onEditFarmedType(result.FarmedData.id);

      } else {
        this._FarmsService.AddSaltTypes = {} as IAddFarmingWays;

      }
    });
  }

  onEditFarmedType(roleId) {
    this._FarmsService.editSalt_types(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Updated successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  openFarmedTypeDeleteDialog(data) {
    const dialogRef = this.dialog.open(DeleteSaltTypesComponent, {
      data: {farmData: data}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onDeleteFarmedType(result);

      }
    });
  }

  onDeleteFarmedType(roleId) {
    this._FarmsService.deleteSalt_types(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

}
