import { Component, OnInit } from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IAddFarmingWays, IAddPlantSize, IFarmedType} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddEditAnimalMedicineComponent} from '../animal-medicine/add-edit-animal-medicine/add-edit-animal-medicine.component';
import {DeleteAnimalMedicineComponent} from '../animal-medicine/delete-animal-medicine/delete-animal-medicine.component';
import {AddEditPlantPotSizesComponent} from './add-edit-plant-pot-sizes/add-edit-plant-pot-sizes.component';
import {DeletePlantPotSizesComponent} from './delete-plant-pot-sizes/delete-plant-pot-sizes.component';

@Component({
  selector: 'app-plant-pot-sizes',
  templateUrl: './plant-pot-sizes.component.html',
  styleUrls: ['./plant-pot-sizes.component.css']
})
export class PlantPotSizesComponent implements OnInit {


  cols: any;
  products: any;
  roles: IAppAdmin[];
  farms: IAddPlantSize[];
  profileFarm: IFarmedType;
  rolesPermations: any[];
  display: boolean = false;
  data: any;
  // toastOption: ToastOptions = {
  //     title: 'Events',
  //     msg: 'This Event Has Been Deleted successfully!',
  //     showClose: true,
  //     timeout: 3000,
  //     theme: 'material'
  // };
  errorMessage: string;
  profileFarmData: IAddFarmedType;

  constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
  }

  //
  ngOnInit(): void {
    this.cols = [
      {field: 'id', header: 'ID'},
      {field: 'size', header: 'Plant Pot Sizes '},


    ];
    this.getFarmsApp();
  }

  // showDialogGetFarmedTypeById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarm = res.data as IFarmedType;
  //         console.log(this.profileFarm);
  //     }, error => {
  //
  //     }, () => {
  //         this.display = true;
  //
  //     });
  // }

  getFarmsApp() {
    this._FarmsService.getPlant_pot_sizes().subscribe((res: { data }) => {
      this.farms = res.data as IAddPlantSize[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }


  openAddFarmedTypeDialog() {

    const dialogRef = this.dialog.open(AddEditPlantPotSizesComponent, {
          data: {
            FarmedData: this._FarmsService.AddPlantPotSizes, error: this.errorMessage,
          }
        })
    ;
    dialogRef.afterClosed().subscribe(result => {
      this.errorMessage = null;

      if (result) {
        this._FarmsService.AddPlantPotSizes = result.FarmedData;
        this.onAddFarmedType();

      } else {
        this._FarmsService.AddPlantPotSizes = {} as IAddPlantSize;

      }
    });
  }

  onAddFarmedType() {
    this._FarmsService.AddPlant_pot_sizes().subscribe(res => {
    }, error => {

      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddPlantPotSizes = {} as IAddPlantSize;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  // getDataById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarmData = res.data as IAddFarmedType;
  //         console.log(this.profileFarmData);
  //     }, error => {
  //
  //     }, () => {
  //         // this.display = true;
  //     });
  //
  //
  // }

  openEditFarmedTypeDialog(classData: IAddPlantSize) {


    const dialogRef = this.dialog.open(AddEditPlantPotSizesComponent, {
      data: {FarmedData: classData}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this._FarmsService.AddPlantPotSizes = result.FarmedData;

        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onEditFarmedType(result.FarmedData.id);

      } else {
        this._FarmsService.AddPlantPotSizes = {} as IAddPlantSize;

      }
    });
  }

  onEditFarmedType(roleId) {
    this._FarmsService.editPlant_pot_sizes(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddPlantPotSizes = {} as IAddPlantSize;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  openFarmedTypeDeleteDialog(data) {
    const dialogRef = this.dialog.open(DeletePlantPotSizesComponent, {
      data: {farmData: data}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onDeleteFarmedType(result);

      }
    });
  }

  onDeleteFarmedType(roleId) {
    this._FarmsService.deletePlant_pot_sizes(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }



}
