import {Component, Inject, OnInit, Output, EventEmitter} from '@angular/core';
import {FarmsService} from '../../../../Services/farms.service';
import {IFarmActivity} from '../../../../Models/farms';
import {ToastaService} from 'ngx-toasta';
import {Form} from '@angular/forms';

// import * as EventEmitter from 'events';

@Component({
    selector: 'app-add-type',
    templateUrl: './add-type.component.html',
    styleUrls: ['./add-type.component.css']
})
export class AddTypeComponent implements OnInit {
    imagePath: any;
    fileToUpload: File = null;
    // farmActivities: IFarmActivity[];
    farmActivity: IFarmActivity[];
    min: number;
    max: number;
    min_water_salts: number;
    max_water_salts: number;
    min_suitable_phs: number;
    max_suitable_phs: number;
    min_suitable_soil: number;
    max_suitable_soil: number;
    min_farming: number;
    max_farming: number;
    min_flowering: number;
    max_flowering: number;
    min_maturity: number;
    max_maturity: number;
    min_humidity: number;
    max_humidity: number;
    min_maturity_time: number;
    max_maturity_time: number;
    min_flowering_time: number;
    max_flowering_time: number;

    @Output() newItemEvent: EventEmitter<File> = new EventEmitter();
    saltArray: number[] = [];
    water_salts: number[] = [];
    suitable_ph: number[] = [];
    suitable_soil: number[] = [];
    farming_temperature: number[] = [];
    flowering_temperature: number[] = [];
    maturity_temperature: number[] = [];
    humidity: number[] = [];
    flowering_time: number[] = [];
    maturity_time: number[] = [];

    constructor(public _FarmsService: FarmsService, private toastyService: ToastaService) {
    }

    addNewItem(value: File) {
        this.newItemEvent.emit(value);
    }

    ngOnInit(): void {
        this.getFarmsActivity();
    }

    onImageChange(event) {
        const reader2 = new FileReader();
        if (event.target.files && event.target.files.length) {
            const [file] = event.target.files;
            reader2.readAsDataURL(file);
            reader2.onload = () => {
                // this.imageSrc = reader2.result as string;
                this.fileToUpload = event.target.files.item(0);
                this.addNewItem(this.fileToUpload);
            };
        }
    }

    onSelectFile(event) {
        // called each time file input changes
        console.log(event);
        if (event.target.files && event.target.files[0]) {
            const reader = new FileReader();

            const [file] = event.target.files;
            // reader2.readAsDataURL(file);
            reader.readAsDataURL(file);

            // tslint:disable-next-line:no-shadowed-variable
            reader.onload = () => {
                this.fileToUpload = event.target.files.item(0);

                this.imagePath = event.target.result;
                console.log(this.fileToUpload);


            };

        }


    }

    getFarmsActivity() {
        this._FarmsService.getFarm_activity().subscribe((res: { data }) => {
            this.farmActivity = res.data.all as IFarmActivity[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }

    onAddFarmedType(data: any) {

        this.min = Number((<HTMLInputElement>document.getElementById('min')).value);
        this.max = Number((<HTMLInputElement>document.getElementById('max')).value);
        this.saltArray.push(this.min, this.max);

        this.min_water_salts = Number((<HTMLInputElement>document.getElementById('min_water_salts')).value);
        this.max_water_salts = Number((<HTMLInputElement>document.getElementById('max_water_salts')).value);
        this.water_salts.push(this.min_water_salts, this.max_water_salts);

        this.min_suitable_phs = Number((<HTMLInputElement>document.getElementById('min_suitable_phs')).value);
        this.max_suitable_phs = Number((<HTMLInputElement>document.getElementById('max_suitable_phs')).value);
        this.suitable_ph.push(this.min_suitable_phs, this.max_suitable_phs);

        this.min_suitable_soil = Number((<HTMLInputElement>document.getElementById('min_suitable_soil')).value);
        this.max_suitable_soil = Number((<HTMLInputElement>document.getElementById('max_suitable_soil')).value);
        this.suitable_soil.push(this.min_suitable_soil, this.max_suitable_soil);

        this.min_farming = Number((<HTMLInputElement>document.getElementById('min_farming')).value);
        this.max_farming = Number((<HTMLInputElement>document.getElementById('max_farming')).value);
        this.farming_temperature.push(this.min_farming, this.max_farming);

        this.min_flowering = Number((<HTMLInputElement>document.getElementById('min_flowering')).value);
        this.max_flowering = Number((<HTMLInputElement>document.getElementById('max_flowering')).value);
        this.flowering_temperature.push(this.min_flowering, this.max_flowering);

        this.min_maturity = Number((<HTMLInputElement>document.getElementById('min_maturity')).value);
        this.max_maturity = Number((<HTMLInputElement>document.getElementById('max_maturity')).value);
        this.maturity_temperature.push(this.min_maturity, this.max_maturity);

        this.min_humidity = Number((<HTMLInputElement>document.getElementById('min_humidity')).value);
        this.max_humidity = Number((<HTMLInputElement>document.getElementById('max_humidity')).value);
        this.humidity.push(this.min_humidity, this.max_humidity);

        this.min_humidity = Number((<HTMLInputElement>document.getElementById('min_humidity')).value);
        this.max_humidity = Number((<HTMLInputElement>document.getElementById('max_humidity')).value);
        this.flowering_time.push(this.min_humidity, this.max_humidity);

        console.log(this.suitable_ph);
        this._FarmsService.AddFarmType.suitable_soil_salts_concentration = this.saltArray;
        this._FarmsService.AddFarmType.suitable_water_salts_concentration = this.water_salts;
        this._FarmsService.AddFarmType.suitable_ph = this.suitable_ph;
        this._FarmsService.AddFarmType.suitable_soil_types = this.suitable_soil;
        this._FarmsService.AddFarmType.farming_temperature = this.farming_temperature;
        this._FarmsService.AddFarmType.flowering_temperature = this.flowering_temperature;
        this._FarmsService.AddFarmType.maturity_temperature = this.maturity_temperature;
        this._FarmsService.AddFarmType.humidity = this.humidity;

        this._FarmsService.AddFarmedType().subscribe(res => {
        }, error => {

            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            // this.getFarmsApp();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }


    addSalt(val: any) {
        this.saltArray.push(val.target.value);
    }
}
