import {Component, Inject, OnInit} from '@angular/core';
import {FarmsService} from '../../../../Services/farms.service';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {IFarmActivity} from '../../../../Models/farms';

@Component({
  selector: 'app-add-edit-class',
  templateUrl: './add-edit-class.component.html',
  styleUrls: ['./add-edit-class.component.css']
})
export class AddEditClassComponent implements OnInit {

  imageSrc: string;
  fileToUpload: File = null;
  // farmActivities: IFarmActivity[];
  farmActivity: IFarmActivity[];

  constructor(public _FarmsService: FarmsService, public dialogRef: MatDialogRef<AddEditClassComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
  }

  ngOnInit(): void {
    this.getFarmsActivity();
  }


  getFarmsActivity() {
    this._FarmsService.getFarmedType().subscribe((res: { data }) => {
      this.farmActivity = res.data.all as IFarmActivity[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }

  onNoClick(): void {

    this.dialogRef.close();
  }

}
