import { Component, OnInit } from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {IAddFarmedType, IAddFarmingWays, IFarmedType} from '../../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {FarmsService} from '../../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddAnimalFodderComponent} from '../animal-fodder/add-animal-fodder/add-animal-fodder.component';
import {DeleteAnimalFodderComponent} from '../animal-fodder/delete-animal-fodder/delete-animal-fodder.component';
import {AddEditAnimalMedicineComponent} from './add-edit-animal-medicine/add-edit-animal-medicine.component';
import {DeleteAnimalMedicineComponent} from './delete-animal-medicine/delete-animal-medicine.component';

@Component({
  selector: 'app-animal-medicine',
  templateUrl: './animal-medicine.component.html',
  styleUrls: ['./animal-medicine.component.css']
})
export class AnimalMedicineComponent implements OnInit {

  cols: any;
  products: any;
  roles: IAppAdmin[];
  farms: IAddFarmingWays[];
  profileFarm: IFarmedType;
  rolesPermations: any[];
  display: boolean = false;
  data: any;
  // toastOption: ToastOptions = {
  //     title: 'Events',
  //     msg: 'This Event Has Been Deleted successfully!',
  //     showClose: true,
  //     timeout: 3000,
  //     theme: 'material'
  // };
  errorMessage: string;
  profileFarmData: IAddFarmedType;

  constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _FarmsService: FarmsService, private toastyService: ToastaService) {
  }

  //
  ngOnInit(): void {
    this.cols = [
      {field: 'id', header: 'ID'},
      {field: 'name_ar_localized', header: 'Animal Medicine (Ar)'},
      {field: 'name_en_localized', header: 'Animal Medicine (En)'},


    ];
    this.getFarmsApp();
  }

  // showDialogGetFarmedTypeById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarm = res.data as IFarmedType;
  //         console.log(this.profileFarm);
  //     }, error => {
  //
  //     }, () => {
  //         this.display = true;
  //
  //     });
  // }

  getFarmsApp() {
    this._FarmsService.getAnimal_medicine().subscribe((res: { data }) => {
      this.farms = res.data.all as IAddFarmingWays[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }


  openAddFarmedTypeDialog() {

    const dialogRef = this.dialog.open(AddEditAnimalMedicineComponent, {
          data: {
            FarmedData: this._FarmsService.AddAnimalMedicine, error: this.errorMessage,
          }
        })
    ;
    dialogRef.afterClosed().subscribe(result => {
      this.errorMessage = null;

      if (result) {
        this._FarmsService.AddAnimalMedicine = result.FarmedData;
        this.onAddFarmedType();

      } else {
        this._FarmsService.AddAnimalMedicine = {} as IAddFarmingWays;

      }
    });
  }

  onAddFarmedType() {
    this._FarmsService.AddAnimal_medicine().subscribe(res => {
    }, error => {

      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddAnimalMedicine = {} as IAddFarmingWays;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  // getDataById(id: number) {
  //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
  //         this.profileFarmData = res.data as IAddFarmedType;
  //         console.log(this.profileFarmData);
  //     }, error => {
  //
  //     }, () => {
  //         // this.display = true;
  //     });
  //
  //
  // }

  openEditFarmedTypeDialog(classData: IAddFarmingWays) {


    const dialogRef = this.dialog.open(AddEditAnimalMedicineComponent, {
      data: {FarmedData: classData}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this._FarmsService.AddAnimalMedicine = result.FarmedData;

        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onEditFarmedType(result.FarmedData.id);

      } else {
        this._FarmsService.AddAnimalMedicine = {} as IAddFarmingWays;

      }
    });
  }

  onEditFarmedType(roleId) {
    this._FarmsService.editAnimal_medicine(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this._FarmsService.AddAnimalMedicine = {} as IAddFarmingWays;

      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

  openFarmedTypeDeleteDialog(data) {
    const dialogRef = this.dialog.open(DeleteAnimalMedicineComponent, {
      data: {farmData: data}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onDeleteFarmedType(result);

      }
    });
  }

  onDeleteFarmedType(roleId) {
    this._FarmsService.deleteAnimal_medicine(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getFarmsApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }


}
